.card {
  display: flex;
  /* align-items: center; */
  background-color: #2e3238;
  padding: 10px;
  margin: 5px 30px;
  border-radius: 8px;
  /* height: 190px; */
  flex-direction: row;
}

.image {
  height: 100%;
}

.content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  display: flex;
  flex: 1 1;
  font-size: 50px;
  color: #fff;
}

.year {
  display: flex;
  flex: 2 1;
  color: #fff;
}

.plot {
  display: flex;
  font-style: italic;
  color: #fff;
  flex: 1 1;
}
